import axios from 'axios';

axios.defaults.timeout = 60 * 1000;
axios.defaults.baseURL = 'http://weibo.jier18cm.xyz:10172';

/**
* 封装get方法
* @param {*} url 
* @param {*} params 
*/
export function get(url, params = {}) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: params
        }).then(response => {
            resolve(response.data);
        }).catch(err => {
            reject(err)
        })
    })
}

/**
 * 封装post请求
 * @param {*} url 
 * @param {*} data 
 */
export function post(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.post(url, data)
            .then(response => {
                resolve(response.data);
            }, err => {
                reject(err)
            })
    })
}

