<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-card-text>
            <v-text-field
              v-model="tel"
              :loading="loading"
              @keyup.enter="getInfo()"
            >
              <v-icon slot="prepend" @click=""
                >mdi-account-circle-outline</v-icon
              >
              <v-icon slot="append-outer" @click="getInfo">mdi-magnify</v-icon>
            </v-text-field>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row align="center">
      <v-card class="mx-auto" tile v-if="info != null">
        <v-list rounded>
          <v-subheader>查询结果</v-subheader>
          <v-list-item-group color="primary">
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-cellphone</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>{{ info.tel }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-message-processing-outline</v-icon>
              </v-list-item-icon>
              <v-list-item-content
                ><a>
                  <v-list-item-title>{{ info.url }}</v-list-item-title></a
                >
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-identifier</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>{{ info.uid }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card>
    </v-row>
    <v-snackbar v-model="snackbar">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="red darken-2"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          关闭
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  components: {},
  data() {
    return {
      tel: null,
      loading: false,
      info: null,
      snackbar: false,
      text: null,
    };
  },
  methods: {
    getInfo() {
      let that = this;
      this.info = null;
      this.loading = true;
      this.$get("/weibo/tel/" + this.tel).then((res) => {
        if (res.message === "Success") {
          that.loading = false;
          that.info = {};
          that.info.tel = res.tel;
          that.info.uid = res.uid;
          that.info.url = res.url;
        } else {
          that.loading = false;
          that.snackbar = true;
          that.text = res.message;
        }
      });
    },
  },
};
</script>
